/* 헤더 영역 */

header {
    position: fixed;
    top: 0px;
    left: 0;
    width: 100%;
    height: 90px;
    z-index: 100;
    transition: .2s;
}

header.active {
    background-color: #1c1c1c;
    transition: .5s;
}

header .logo {
    width: 120px;
    color: #FFF;
}

header .container {
    width: 80%;
    height: 100%;
    margin: 0 auto;
    justify-content: space-between;
    align-items: center;
}

header.active .container {
    width: 80%;
    height: 100%;
    margin: 0 auto;
    justify-content: space-between;
    align-items: center;
}

header .menu_wrap {
    width: 400px;
    display: flex;
    gap: 10px;
    flex-direction: column;

}

header .menu_wrap .top_menu {
    justify-content: flex-end;
    align-items: center;
    font-size: 12px;
    color: rgb(194, 192, 192);
    gap: 1rem;
    text-transform: capitalize;
}

header .menu_wrap .top_menu select {
    background: none;
    border: none;
}

header .menu_wrap .bottom_menu {
    height: 100%;
    align-items: center;
    color: #fff;
}

header .menu_wrap .item {
    flex: 1;
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

header .menu button {
    border-radius: 5px;
    background-color: #fff;
    color: #424242;
    padding: .2rem .6rem;
    font-size: 14px;
    font-weight: 700;
}



@media (max-width: 768px) {
    .mobile {
        display: flex;
    }

    .pc {
        display: none;
    }

    @keyframes close {
        from {
            top: 0%;
        }

        to {
            top: -100%;
        }
    }

    @keyframes open {
        from {
            top: -100%;
        }

        to {
            top: 0%;
        }
    }

    header {
        height: 80px;
    }


    .mobile_header .show_menu {
        margin: 0 auto;
        padding: 1rem 0rem;
        width: 90%;
        display: flex;
        align-items: center;
        justify-content: space-between;

    }

    .mobile_header .show_menu .img_wrap {
        width: 20vw;
        height: 50px;
        max-width: 200px;
        display: flex;
        align-items: center;
    }

    .mobile_header .show_menu .img_wrap img {
        height: 100%;
    }



    .menu_layer {
        position: fixed;
        top: -100%;
        height: 100vh;
        overflow: hidden;
        left: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.468);
        backdrop-filter: blur(15px);
        z-index: 100;

    }

    .menu_layer.off {
        animation: close .5s forwards
    }

    .menu_layer.open {
        animation: open .7s forwards
    }

    .menu_layer>.mobile_wrpper {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        padding-top: 6vh;
        width: 100%;
        height: 100vh;
        box-sizing: border-box;
        background: #000;

    }

    .menu_layer>.mobile_wrpper>.modile_menu_wrap {
        width: 88%;
        margin: 0 auto;
        flex-direction: column;
        /* grid-template-rows: ; */
    }

    .menu_layer>.mobile_wrpper>.modile_menu_wrap>.menu_content {
        width: 100%;
        height: 50px;
        justify-content: space-between;
        display: flex;
    }

    .menu_layer>.mobile_wrpper>.modile_menu_wrap>.menu_content .menu_icon {
        align-items: center;
        display: flex;
    }

    .menu_layer>.mobile_wrpper>.modile_menu_wrap>.menu_content .menu_icon svg {}


    header .container .icon_wrap {
        width: 10vh;
        max-width: 25px;
    }

    header .top_menu {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        color: #fff;
        margin-top: 10vh;
    }

    header .top_menu .menu_item {
        display: flex;
        justify-content: space-between;
        gap: 5rem;
        font-size: 1rem;

    }

    header .top_menu .user_info_wrap {
        width: 100%;
        padding: 1rem .5rem;
        display: flex;
        border-radius: 10px;
        background-color: #2b2a25;
        font-size: 1rem;
        color: #fff;
    }

    header .top_menu .user_info_wrap .user_info {
        width: 100%;
        display: flex;
        justify-content: space-between;
    }

    header .bottom_menu {
        position: absolute;
        bottom: 2vh;
        width: calc(100% - 40px);
    }

    header .bottom_menu .select_wrap {
        margin-top: 27vh;
        display: flex;
        justify-content: flex-end;
    }

    header .bottom_menu .select_wrap select {
        border: none;
        padding: .5rem .5rem;
        border-radius: 5px;
    }

}