.wrpper {
    position: relative;
    width: 285px;
    height: 504px;
}

.popup_wrap {
    width: 100%;
    height: 100%;
    border-radius: 16px;
    /* background: linear-gradient(to bottom,
            rgba(255, 255, 255, 0) 10%,
            rgba(255, 255, 255, 0.25) 25%,
            rgba(255, 255, 255, 0.5) 50%,
            rgb(255, 255, 255, 0.8) 50%,
            rgb(234, 234, 234) 60%,
            rgb(234, 234, 234) 100%), url("../../../assets/images/야간 루프탑.jpg"); */
    background-size: cover;
    box-shadow: 3px 5px 8px rgba(0, 0, 0, 0.33);
}

.container {
    margin: 0 auto;
    padding-bottom: 22px;
    width: 90%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 10px;
}

.container .text1 {
    display: flex;
    align-items: flex-end;
    gap: 5px;

}

.container .text1>* {
    font-weight: bold;
}

.container .text1 h2 {
    font-size: 22px;

}

.container .content {
    border-radius: 16px;
    padding: 8px 8px 20px 8px;
    background-color: #fff;
    font-size: 11px;
    font-weight: 200;
}

.container .content .title_wrap {
    display: flex;
    align-items: center;
    gap: 5px;
}

.container .content .title_wrap img {
    width: 30px;
    height: 30px;
    object-fit: cover;
    border: 1px solid #898989;
    border-radius: 50%;
    overflow: hidden;
}

.container .phoneNum {
    font-size: 12px;
    color: #000;
    font-weight: bold;
}

.container button {
    font-size: 12px;
    padding: 10px;

}

.float_wrap {
    position: absolute;
    right: 0;
    bottom: -50px;

}

.float_wrap .open_wrap {
    display: flex;
    align-items: center;

}

.float_wrap .open_wrap .inquiry {
    position: relative;
    padding-left: 18px;
    width: 140px;
    height: 45px;
    line-height: 45px;
    border-radius: 20px;
    background-color: #fff;
    box-shadow: 3px 5px 8px rgba(0, 0, 0, 0.33);
    font-size: 14px;
}

.float_wrap .open_wrap .img_box {
    position: absolute;
    right: -2px;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #000;
    box-shadow: 3px 5px 8px rgba(0, 0, 0, 0.33);
    border-radius: 50%;
}

.float_wrap .open_wrap .img_box img {
    width: 35px;
    height: 35px;
}

.float_wrap .close_wrap {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f8f8f9;
    box-shadow: 3px 5px 8px rgba(0, 0, 0, 0.33);
    border-radius: 50%;
}