.menu_wrap {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 35px;
}

.menu_items {
    display: flex;
    flex-direction: column;
    gap: 15px;
    cursor: pointer;
}

.menu_items>.menu_title {
    color: #A0A0A0;
    font-weight: 300;
}

.menu_items>.menu_item {
    width: 100%;
    height: 55px;
    line-height: 55px;
    border-radius: 5px;
    padding-left: 1rem;
    color: #333;
    font-size: 1.3rem;
}

.menu_items>.menu_item.active {
    background-color: #efe8e0;
    font-weight: 700;
    color: #BC9F7C;
}
.icon_wrap { 
    display: none;
}
@media (max-width: 1024px) { 
    .active {
        background-color: #fff !important;
    }
    .menu_wrap { 
        padding: 50px;
        z-index: 99;
        position: fixed;
        top: 80px;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #000;

    }

    .menu_items>.menu_item { 
        color: #fff;
    }

    .icon_wrap {
        display: block;
        position: absolute;
        top: 2%;
        right: 5%;
    }
}