 /* *{display: none;} */

 /* 슬라이드 영역 */
 main#homePage #slide .slide {
     width: 100%;
     height: calc(100vh);
     position: relative;
     background-size: cover;
     background-position-y: center;
     background-repeat: no-repeat;
 }

 main#homePage #slide img {
     width: 100%;
     height: 100%;
     filter: brightness(.6);
     object-fit: fill;
 }

 main#homePage #slide .text {
     position: absolute;
     width: 100%;
     top: 30%;
     z-index: 1;
     text-align: center;
     color: #ebebeb;
     font-size: 6rem;
     font-weight: 500;
 }

 main#homePage #slide .slide_ctr {
     width: 550px;
     height: 120px;
     margin: 0 auto;
     position: absolute;
     top: calc(100% - 170px);
     z-index: 100;
     left: calc(100vw / 2 - 275px);
     justify-content: center;
     align-items: center;
     /* border-radius: 20px; */
     background-color: #1c1c1cd0;
 }

 main#homePage #slide .slide_ctr>div {
     display: flex;
     justify-content: center;
     align-items: flex-end;
 }

 main#homePage #slide .slide_ctr .item {
     padding-bottom: .5rem;
     display: flex;
     justify-content: space-between;
     border-bottom: 1px solid #fff;
     color: #fff;
 }

 main#homePage #slide .slide_ctr .item .txt1 {
     text-align: center;
     margin-bottom: .4rem;
 }

 main#homePage #slide .slide_ctr .item input {
     width: 150px;
     background-color: unset;
     outline: unset;
     border: unset;
     text-align: center;
     cursor: pointer;
 }

 main#homePage #slide .slide_ctr .item .checkday {
     width: 60px;
     text-align: center;
 }

 main#homePage #slide .slide_ctr .item .checkday .txt1 {
     width: 20px;
     height: 20px;
     margin: 0 auto .5rem;
     display: flex;
     justify-content: center;
     align-items: center;
     background: #fff;
     color: #1c1c1c;
     /* border-radius: 50%; */
     font-size: .5rem;
 }

 main#homePage #slide .slide_ctr .not_item {
     background-color: #1c1c1c;
     color: #e2e2e2;
     text-align: center;
 }

 main#homePage #slide .slide_ctr .item .click_visble_wrap {
     position: relative;
     width: 110px;
     text-align: center;
     cursor: pointer;
 }

 main#homePage #slide .slide_ctr .item .click_visble_wrap .visable_wrap {
     position: absolute;
     top: -45px;
     padding: .5rem 0rem;
     width: 100%;
     display: flex;
     justify-content: center;
     align-items: center;
     gap: 1rem;
     background-color: #fff;
     /* border-radius: 10px; */
     color: #1c1c1c;
 }

 main#homePage #slide .slide_ctr .item .click_visble_wrap .visable_wrap i {
     width: 25px;
     height: 25px;
     display: flex;
     justify-content: center;
     align-items: center;
     border: 1.5px solid #1c1c1c;
     /* border-radius: 50%; */
     font-size: .6rem;
     color: #1c1c1c;
 }


 /* 갤러리 영역 */
 #homePage .hotelInfo_container {
     padding-top: 140px;
     width: 100%;
     height: 920px;
     grid-template-columns: 560px auto;
 }

 #homePage .hotelInfo_container .title_wrap {
     margin-left: 160px;
 }

 #homePage .hotelInfo_container .title_wrap .sub_title {
     color: #BC9F7C;
 }

 #homePage .hotelInfo_container .img_wrap_scroll {
     display: flex;
     width: calc(100% - 20px);
     height: 620px;
     gap: 2rem;
     overflow-y: auto;
 }

 #homePage .hotelInfo_container .img_wrap_scroll .img_box {
     min-width: 400px;
     height: 550px;
 }

 #homePage .hotelInfo_container .img_wrap_scroll .img_wrap_text {
     font-weight: bold;
 }

 #homePage .hotelInfo_container .img_wrap_scroll .img_box>img:nth-child(1) {
     filter: brightness(.7);
 }

 /* 방 종류 */

 /* #homePage .roomImg_wrapper .title_wrap {text-align: right;} */
 #homePage #roomType {
     padding: 5rem 0rem;
     background-color: #f3f3f3;

 }

 #homePage .roomType_wrapper {
     flex-grow: 1;
 }


 #homePage .roomImg_wrapper .container {
     height: 100%;
     gap: 2rem;
     justify-content: space-between;
 }

 #homePage .roomImg_wrapper .container .title_wrap {
     height: 500px;
     display: grid;
     grid-template-rows: auto 1fr;
     /* justify-content: space-between; */
     color: #333;
 }

 #homePage .roomImg_wrapper .container .title_wrap .type_info {
     /* flex-grow: 1; */
     display: grid;
     grid-template-rows: auto 1fr;
     gap: 1rem;
 }

 #homePage .roomImg_wrapper .roomType_info {
     display: flex;
     gap: 2rem;
 }

 #homePage .roomImg_wrapper .roomType_info .text {
     display: grid;
     gap: 1rem;
 }


 #homePage .roomImg_wrapper .roomType_info .entry_list {
     display: flex;
     justify-content: space-between;
 }

 #homePage .roomImg_wrapper .roomType_info .entry {
     display: flex;
     gap: 1rem;
 }

 #homePage .roomImg_wrapper .roomType_info .roomType_title {
     font-size: 3rem;
     font-weight: bold;
 }

 #homePage .roomImg_wrapper .roomType_info .type_room_wrap {
     flex-grow: 1;
     height: 500px;
     /* border-radius: 16px; */
     overflow: hidden;
 }


 #homePage .roomImg_wrapper .roomType_wrap {
     padding-top: 2.4rem;
     grid-template-columns: repeat(3, 170px);
     gap: 1.5rem;
     /* justify-content: end; */
 }

 #homePage .roomImg_wrapper .roomType_wrap .img_box {
     position: relative;
     height: 170px;
     /* border-radius: 14px; */
     overflow: hidden;
     filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
 }

 #homePage .roomImg_wrapper .roomType_wrap .img_box img {
     filter: brightness(.6);
 }

 #homePage .roomImg_wrapper .roomType_wrap .img_box .roomType_text {
     position: absolute;
     top: 0;
     left: 0;
     z-index: 2;
     width: 100%;
     height: 100%;
     display: flex;
     justify-content: center;
     align-items: center;
     font-size: 22px;
     font-weight: bold;
     color: #BC9F7C;
 }

 #homePage .roomImg_wrapper .roomType_wrap .img_box.active img {
     transition: .3s;
     filter: brightness(1);
 }



 /* 이태원 tour */
 #homePage #tourWrap {
     padding-top: 140px;
     padding-bottom: 140px;
 }

 #homePage #tourWrap .title_wrap {
     padding-bottom: 70px;
 }

 #homePage #tourWrap .title_wrap>div {
     text-align: center;
 }

 #homePage .tourWrap {
     gap: 1rem;
 }

 #homePage .tourWrap .img_line_wrap {
     flex-direction: column;
     gap: 1rem;
 }

 #homePage .tourWrap .img_line_wrap img {
     /* filter: brightness(.9); */
 }

 /* 방 영역 */
 #homePage #room {
     background-color: #f9f9f9;
     padding: 6rem 0rem;
 }

 #homePage #room .slide_wrap {
     position: relative;
 }

 #homePage #room .slide_wrap i {
     position: absolute;
     top: 50%;
     padding: .35rem;
     /* border-radius: 50%; */
     color: #fff;
     z-index: 10;
     transition: .3s;
 }

 #homePage #room .slide_wrap:hover i {
     transition: .3s;
 }

 #homePage #room .slide_wrap .fa-arrow-left {
     left: 10px;
 }

 #homePage #room .slide_wrap .fa-arrow-right {
     right: 10px;
 }

 #homePage #room .content_wrap {
     grid-template-columns: repeat(4, 300px);
     gap: 1rem;
 }

 #homePage #room .content_wrap .roomType {
     cursor: pointer;
     border: 2px solid #fff;
     position: relative;
 }

 #homePage #room .content_wrap .roomType.active .img_box {
     filter: brightness(1);
     transition: .3s;
 }

 #homePage #room .content_wrap .roomType.active .active_btn {
     background-color: #bc9f7c;
     color: #fff;
     transition: .3s;
 }

 #homePage #room .content_wrap .roomType .active_btn {
     width: 60px;
     line-height: 25px;
     display: flex;
     justify-content: center;
     top: 10px;
     left: 10px;
     position: absolute;
     z-index: 10;
     background-color: #fff;
     font-size: 13px;
     color: #1c1c1c;
     transition: .3s;
 }

 #homePage #room .content_wrap .item .img_box {
     height: 300px;
     filter: brightness(.6);
     transition: .3s;
 }

 #homePage #room .content_wrap .item .text_wrap {
     padding: .5rem;
     display: grid;
     gap: .8rem;
     color: #424242;
 }

 #homePage #room .content_wrap .item .text_wrap h2 {
     font-weight: 700;
     font-size: 2rem;
 }

 #homePage #room .content_wrap .item .text_wrap button {
     width: 250px;
     background-color: #242424;
     color: #fff;
     line-height: 35px;
 }

 #homePage #room .content_wrap .item:nth-child(1) {
     grid-column: 1/3;
     grid-row: 1/3;
 }

 #homePage #room .content_wrap .item:nth-child(1) .img_box {
     height: 350px;
     filter: brightness(1);
 }

 /* 지역영역 */
 #homePage #location {
     display: flex;
 }

 #homePage #location>div {
     flex: 1;
     width: 100%;
 }

 #homePage #location .loction_wrap {
     height: 900px;
     grid-template-columns: repeat(2, 1fr);
 }

 #homePage #location .loction_wrap .img_box {
     height: 100%;
     filter: brightness(.7);
 }

 #homePage #location .right {
     padding: 4rem;
     display: flex;
     flex-direction: column;
     background-color: #111;
 }

 #homePage #location .right .m_title {
     line-height: 70px;
     font-size: 5rem;
     color: #fff;
 }

 #homePage #location .right .tm_text {
     font-size: 14px;
     color: #fff;
 }

 #homePage #location .right .m2_title {
     line-height: 80px;
     font-size: 5rem;
     color: #bc9f7c;
 }

 #homePage #location .right .text_wrap {
     display: flex;
     padding: 5rem 0rem;
     gap: 2rem;
     color: #bebebe;
 }

 #homePage #location .right .text_wrap p {
     flex: 1;
 }

 #homePage #location .right button {
     width: 350px;
     background-color: #242424;
     border: unset;
     color: #fff;
     line-height: 40px;
 }

 /* 지역영역밑에 텍스트 */
 #homePage article {
     width: 1000px;
     margin: 3rem auto;
     text-align: center;
 }

 /* 그리드 이미지 영역 */
 #homePage #grid_img {
     padding: 5rem 0rem;
     background-color: #f9f9f9;
 }

 #homePage #grid_img .container {
     display: grid;
     grid-template-columns: repeat(2, 1fr);
 }

 #homePage #grid_img .container>div {
     height: 550px;
 }

 #homePage #grid_img .container>div img {
     filter: brightness(.6);
 }

 #homePage #grid_img .container .text_item {
     color: #424242;
 }

 #homePage #grid_img .container .text_item {
     padding: 0rem 3rem;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     gap: 2rem;
     background-color: #fff;
     text-align: center;
 }

 #homePage #grid_img .container .text_item .t_title {
     color: #bc9f7c;
 }

 #homePage #grid_img .container .text_item h2 {
     font-size: 2rem;
     font-weight: 700;
 }

 #homePage #grid_img .container .text_item button {
     width: 150px;
     line-height: 35px;
     background: #fff;
     border: 1px solid #bc9f7c;
     font-weight: 700;
     color: #bc9f7c;
 }

 /* 백그라운드 이미지 영역 */
 #homePage #backImg .img_box {
     height: 400px;
     position: relative;
 }

 #homePage #backImg .img_box img {
     filter: brightness(.8);
 }

 #homePage #backImg .img_box .text_box {
     position: absolute;
     width: 100%;
     height: 100%;
     top: 0;
     left: 0;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     gap: 1rem;
     z-index: 1;
     color: #fff;
 }

 #homePage #backImg .img_box .text_box .bigText {
     line-height: 50px;
     color: #fff;
     font-size: 3rem;
 }

 #homePage #backImg .img_box .text_box .cial {
     width: 50px;
     height: 50px;
     border: 3px solid #fff;
     /* border-radius: 50%; */
 }

 /* 블로그 영역 */
 #homePage #blog {
     padding: 5rem 0rem;
     background-color: #f9f9f9;
 }

 #homePage #blog .blog_grid {
     display: grid;
     gap: 1rem;
     grid-template-columns: repeat(3, 1fr);
 }

 #homePage #blog .blog_grid .item {
     display: flex;
     flex-direction: column;
     gap: 1rem;
 }

 #homePage #blog .blog_grid .item img {
     filter: brightness(.9)
 }

 #homePage #blog .blog_grid .item .img_box {
     position: relative;
 }

 #homePage #blog .blog_grid .text_btn_box {
     padding: 2rem 1rem;
     display: flex;
     flex-direction: column;
     gap: 1rem;
     background-color: #fff;
     color: #424242;
 }

 #homePage #blog .blog_grid .text_btn_box button {
     width: 150px;
     line-height: 34px;
     border: 1px solid #bc9f7c;
     background-color: #fff;
     color: #bc9f7c;
 }

 #homePage #blog .blog_grid .text_btn_box h2 {
     font-weight: 700;
 }

 #homePage #blog .blog_grid .line1 .img_box {
     height: 290px;
 }

 #homePage #blog .blog_grid .line1 .text_box {
     height: 150px;
     background: #242424;
     color: #fff;
     text-align: center;
     font-weight: 700;
     display: flex;
     justify-content: center;
     align-items: center;
 }

 #homePage #blog .blog_grid .line2 .text_box {
     height: 220px;
     padding: 1rem;
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: space-around;
     background-color: #bc9f7c;
     text-align: center;
     color: #fff;
 }

 #homePage #blog .blog_grid .line2 .img_box {
     height: 220px;
 }

 #homePage #blog .blog_grid .line3 .img_box {
     height: 760px;
 }

 @media (max-width: 1280px) {
     #homePage .hotelInfo_container .img_wrap_scroll {
         grid-template-columns: repeat(3, 1fr);
     }

     #homePage .hotelInfo_container .img_wrap_scroll .img_box {
         height: 450px;
     }
 }

 @media (max-width: 1080px) {
     #homePage .hotelInfo_container .img_wrap_scroll {
         grid-template-columns: repeat(2, 1fr);
     }
 }


 @media (max-width: 768px) {

     .title_wrap .main_title {
         font-size: 40px;
     }

     #homePage .hotelInfo_container {
         /* height: 2700px; */
         padding: 80px 0px;
         grid-template-columns: none;
         /* grid-template-rows: 100px auto; */
     }

     #homePage .hotelInfo_container .title_wrap {
         margin: 0 auto;
     }

     #homePage .hotelInfo_container .title_wrap .sub_title {
         text-align: center;
     }

     #homePage .hotelInfo_container .img_wrap_scroll {
         grid-template-columns: repeat(1, 1fr);
         width: 90%;

         /* height: 460px; */
         margin: 0 auto;
     }

     #homePage .hotelInfo_container .img_wrap_scroll .img_box {
         height: 400px;
     }

     #homePage .roomImg_wrapper .container {
         flex-direction: column-reverse;
     }

     #homePage .roomImg_wrapper .drak_wrap {
         width: 100%;
         height: 50%;
     }

     #homePage .roomImg_wrapper .container>.roomType_img_wrap {
         padding-top: 30px;
     }

     #homePage .tourWrap {
         display: grid;
         gap: .2rem;
         /* grid-template-columns: repeat(2, 1fr); */
     }

     #homePage #tourWrap .title_wrap {
         padding-bottom: 30px;
     }

     #homePage .tourWrap .img_line_wrap {
         gap: .5rem;
         flex-direction: row;
     }

     /* #homePage .tourWrap .img_line_wrap .img_box{
        height: 200px;
    } */
     #homePage .tourWrap .img_line_wrap .img_box img {
         height: 200px;
     }

     #homePage .roomImg_wrapper .roomType_info {
         flex-direction: column;
         gap: 1rem;
     }

     #homePage .roomImg_wrapper .container .title_wrap {
         height: 250px;
     }

     #homePage .roomImg_wrapper .roomType_wrap {
         padding: 0rem;
         display: flex;
         flex-wrap: wrap;
         gap: 1.4rem;
     }

     #homePage .roomImg_wrapper .roomType_wrap .img_box {
         width: 120px;
         height: 120px;
     }

     #homePage #tourWrap {
         padding-top: 70px;
     }
 }