.mypage-wrap {
    margin: 90px 0 200px 0;
}

.mypage-title {
    line-height: 84px;
    background-color: var(--main-bg-color);
    color: #fff;
    font-size: 1.5rem;
}

.mypage-content {
    display: grid;
    gap: 70px;
    grid-template-columns: 17% 70%;
    margin-top: 65px;
}

.mypage-content > .page-wrap > .menu-name {
    font-size: 2rem;
    text-align: center;
    margin-bottom: 75px;
}

.inquiryPopup_wrap {
    position: fixed;
    top: calc(90% - 500px);
    right: 10px;
}



@media (max-width: 1024px) { 
    .container {
        width: 95%;
    }
    .mypage-title {
        font-size: 1rem;
        line-height: 50px;
    }
    .mypage-title .container{
        display: flex;
        justify-content: space-between;
    }
    
    .mypage-content {
        grid-template-columns: 100%;
    }

    .mypage-content > .page-wrap > .menu-name { 
        font-size: 1.5rem;
        margin-bottom: 20px;
    }

    .inquiryPopup_wrap {
        z-index: 20;
        position: fixed;
        top: calc(90% - 500px);
        right: 10px;
    }
}

@media (max-width: 768px) { 
    .mypage-wrap {
        margin: 80px 0 200px 0;
    }
}