
 .sign_item {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.signbtn_item {
    display: grid;
    grid-template-columns: 1fr 20% 20%;
    gap: 10px;
    align-items: flex-end;
}

.signbtn_item button {
    height: 45px;
    background-color: #efe8e0;
    color: #fff;
}

.sign_item > label {
    font-size: 14px;
    color: #333;
    font-weight: 600;
}

.signform {
    
    display: grid;
    gap: 20px;
    margin: 50px auto 0;
    /* width: 80%; */
}

.signform .topline {
    
    margin: 35px 0;
    border-top: 1px solid #C3C3C3;
}

.two_item {
    display: flex;
    gap: 20px;
}

.two_item .sign_item {
    
    flex: 1;
}

.btn_wrap {
    display: flex;
    width: 60%;
    justify-self: center;
    gap: 20px;
}

.btn_wrap button {
    flex: 1;
}

.out_wrap {
    padding: 30px 20px;
    display: flex; 
    justify-content: space-between;
    border-radius: 5px;
    background-color: #f4f4f4;
    color: #999;
    font-size: 18px;
    
}