.subtext {
    text-align: center;
}

.sign_item {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.sign_item > label {
    font-size: 14px;
    color: #333;
    font-weight: 600;
}

.signform {
    margin: 50px auto 0;
    /* width: 80%; */
}

.signform button {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    max-width: 330px;
    width: 30%;
}

.signform .topline {
    margin: 35px 0;
    border-top: 1px solid #C3C3C3;
}


@media (max-width: 768px) { 
    .signform {
        width: 100%;
    }

    .signform .topline {
        margin: 20px 20px 40px;
    }

    .subtext {
        font-size: 14px;
    }
}