
.signform {
    margin: 165px auto 150px;
    padding: 100px;
    width: 50%;
    min-width: 640px;
    display: grid;
    gap: 20px;
    /* border: 1px solid rgb(154, 154, 154); */
    border-radius: 10px;
}

.signform .topline {
    
    margin: 35px 0;
    border-top: 1px solid #C3C3C3;
}

.logo {
    width: 200px;
    margin: 0 auto;
}

.sign_item {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.signbtn_item {
    display: grid;
    grid-template-columns: calc(60% - 20px) 20% 20%;
    gap: 10px;
    align-items: flex-end;
}

.signbtn_item button {
    height: 45px;
    background-color: #efe8e0;
    color: #fff;
}

.sign_item > label {
    font-size: 14px;
    color: #333;
    font-weight: 600;
}

.two_item {
    display: flex;
    gap: 20px;
}

.two_item .sign_item {
    
    flex: 1;
}

.btn_wrap {
    margin-top: 50px;
    display: flex;
    width: 60%;
    justify-self: center;
    gap: 20px;
}

.btn_wrap button {
    flex: 1;
}

.out_wrap {
    padding: 30px 20px;
    display: flex; 
    justify-content: space-between;
    border-radius: 5px;
    background-color: #f4f4f4;
    color: #999;
    font-size: 18px;   
}


@media (max-width: 768px) { 
    .logo {
        width: 150px;
    }

    .signform {
        margin: 120px auto 80px;
        padding: 0px;
        width: 90%;
        min-width: unset;
        gap: 15px;
    }

    .signbtn_item { 
        grid-template-columns: repeat(2, 1fr);
    }

    .signbtn_item>div:nth-child(1) { 
        grid-column: 1/3;
    }

    .two_item {
        /* display: flex; */
        flex-direction: column;
        gap: 10px;
    }

    .sign_item { 
        gap: 10px;
    }

    .sign_item input{
        width: 100%;
    }  

    .signform button input, 
    .signform button input::placeholder, 
    .signform button { 
        font-size: 12px;
    }

    .sign_item > label { 
        font-size: 12px;
    }
    .btn_wrap {
        margin-top: 20px;
    }
}
