label {
    color: #333;
    font-size: 14px;
}

.login_form_wrap {
    padding: 10rem 0rem;
}


.login_form_wrap {
    width: 450px;
    margin: 0 auto;
}

.login_form_wrap .logo {
    width: 200px;
    margin: 0 auto;
}

.login_form_wrap form {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.login_form_wrap form input {
    width: 100%;
    border: 1px solid #999;
    outline: none;
    padding: 1rem .5rem;
    border-radius: 4px;
}

.login_form_wrap form button {
    width: 100%;
    height: 50px;
    background-color: #c09c70;
    border: none;
    outline: none;
    text-align: center;
    color: #fff;
    font-size: 20px;
}

.login_form_wrap .other_btn_wrap {
    padding: .6rem .1rem;
    margin: 20px auto;
    width: 100%;
    display: flex;
    color: #333;
    font-size: 20px;
    border: 1px solid #333;
}

.login_form_wrap .other_btn_wrap>div {
    width: 100%;
    flex: 1;
    text-align: center;
}

.login_form_wrap .sns_login_wrap .sns_btn {
    margin-bottom: 20px;
    display: grid;
    grid-template-columns: 50px auto;
    align-items: center;
    border: 1px solid #999;
    border-radius: 5px;
    overflow: hidden;
}

.login_form_wrap .sns_login_wrap .sns_btn i {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(246, 246, 246);
}

.login_form_wrap {
    >form>.item {
        display: grid;
        gap: 1rem;
    }
}

.login_form_wrap .sns_login_wrap .sns_btn .fa-facebook-f {
    color: #414c8d;
}

.login_form_wrap .sns_login_wrap .sns_btn .fa-google {
    color: red;
}

.login_form_wrap .sns_login_wrap .sns_btn .sns_text1 {
    padding: 0rem .6rem;
    font-size: 20px;
    color: #333;
}

@media (max-width: 768px) {
    .login_form_wrap {
        width: 90%;
        margin: 0 auto;
    }

    .login_form_wrap form {
        gap: 1rem;
    }

    label {
        color: #333;
        font-size: 12px;
    }

    button,
    .other_btn {
        font-size: 16px !important;
    }

}