:root {
    --swiper-theme-color: #fff !important;
}

.title_img {
    position: relative;
}

.title_img img {
    filter: brightness(.6);
}

.title_img .title_wrap {
    position: absolute;
    top: 90px;
    left: 100px;
    z-index: 2;
}

.title_img .title_wrap .main_title {
    color: #fff;
    font-size: 50px;
}

#roomDetali {
    padding: 6rem 0rem;
    position: relative;
}

#roomDetali.grid .item_img_wrap {
    display: none;
}

#roomDetali.grid .item_text_wrap {
    width: 100%;
}

#roomDetali.grid .room_list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

#roomDetali.grid .room_item {
    padding: 0px;
}

#roomDetali .toggle_wrap {
    position: fixed;
    right: 1%;
    bottom: 2.5%;
    z-index: 10;
    /* border-radius: 20px; */
    background-color: var(--main-bg-color);
    overflow: hidden;
    cursor: pointer;
}

#roomDetali .toggle_wrap>div.active {
    background-color: var(--main-bg-color);
    /* border-radius: 50%; */

}

#roomDetali .toggle_wrap .text1 {
    font-size: 12px;
}

#roomDetali .toggle_wrap>div {
    width: 55px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2px;
    flex-direction: column;
    background-color: #222;
    color: #fff;


}

.selectDate_wrap {
    margin: 2rem auto;
    width: 70%;
    height: 50px;
    display: flex;
    /* border-radius: 10px; */
    overflow: hidden;
    box-shadow: 3px 5px 8px rgba(0, 0, 0, 0.33);

}

.selectDate_wrap>input {
    flex: 1;
    padding: 0rem 1rem;
    outline: none;
    /* border-radius: 5px; */
    border: 1px solid #000;
}

.selectDate_wrap>input:nth-child(1) {
    background-color: #e9e9e9;
}

.selectDate_wrap>input {
    flex: 1;
    padding: 0rem 1rem;
    /* border-radius: 5px; */
    outline: none;
    border: none;
}

.selectDate_wrap>button {
    flex: 1.2;
}

.container {
    /* width: 90%; */
    margin: 0 auto;
    max-width: 1920px;
}

.room_list {
    padding: 1rem 0rem;
    display: flex;
    flex-direction: column;
    gap: 9rem;

}

.room_item {
    /* padding: 10px; */
    width: 100%;
    overflow: hidden;
    display: grid;
    align-items: center;
    gap: 20px;
    grid-template-columns: 35% calc(65% - 20px);
    /* border-radius: 25px; */
    /* border: 3px solid #666; */
}


.room_item.leftItem {
    display: flex;
    flex-direction: row-reverse;
    padding-right: 12%;
}

.room_item.rightItem {
    display: flex;
    flex-direction: row;
    padding-left: 12%;
}

.room_item.leftItem .item_img_wrap,
.room_item.rightItem .item_img_wrap {
    width: 1100px;
}

.room_name {
    font-weight: bold;
    font-size: 44px;
    color: #333;
}

.room_item>.item_text_wrap {
    width: 40%;
    padding: 20px 20px 0px 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}


.room_item>.item_text_wrap .line_wrap {
    margin-top: 20px;
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 18px;
    color: #666;

}

.room_item>.item_text_wrap .line_wrap .line {
    flex-grow: 1;
    height: 1px;
    background-color: #999;
}


.main_option {
    margin-top: 20px;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 10px;
}

.main_option .option_item {
    position: relative;
    text-align: center;
    display: grid;
    gap: 5px;
}

.main_option .option_item:nth-child(2)::after,
.main_option .option_item:nth-child(3)::after,
.main_option .option_item:nth-child(5)::after,
.main_option .option_item:nth-child(6)::after {
    content: '';
    position: absolute;
    width: 1px;
    height: 70%;
    top: 15%;
    background-color: #999;

}


.main_option .option_item .o_text1 {
    font-size: 22px;
    font-weight: bold;
}

.main_option .option_item .o_text2 {
    color: #666;
}

.sub_option {
    margin-top: 15px;
    gap: 10px;
    flex-wrap: wrap;
}

.sub_option .s_item {
    min-width: 70px;
    text-align: center;
    padding: 11px 10px;
    /* border-radius: 18px; */
    border: 1px solid #777;
    color: #777;
    font-size: 14px;
}

.sub_option .s_item.active {
    background-color: var(--main-bg-color);
    border: 0px;
    color: #fff;

}

.booking {
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    /* border-radius: 10px; */
    margin-top: 10px;
}

.room_item>.item_text_wrap .room_info {
    margin-top: 20px;
    display: grid;
    color: #666;
    line-height: 180%;
}

.room_item .item_img_wrap {
    width: 100%;
    height: 700px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    /* border-radius: 25px; */
    overflow: hidden;
}

.room_item .item_img_wrap .mySwiper {
    width: 100%;
    height: 100%;
}

.room_item .item_img_wrap .bookNow {
    background-color: #bc9f7c;
    color: #fff;
    border: none;
    outline: none;
    padding: .7rem 1rem;
}

@media (max-width: 1600px) {
    .main_option {
        grid-template-columns: repeat(3, 1fr);
    }

    .main_option .option_item .o_text1 {
        font-size: 20px;
    }
}

@media (max-width: 1400px) {

    .container {
        width: 95%;
    }
}

@media (max-width: 1300px) {
    #roomDetali.grid .room_list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    }

    .room_name {
        font-size: 35px;
    }

    .main_option {
        margin-top: 20px;
    }

    .main_option .option_item .o_text1 {
        font-size: 16px;
    }

    .main_option .option_item .o_text2 {
        font-size: 14px;
    }

    .sub_option {
        column-gap: 3px;
    }

    .sub_option .s_item {
        font-size: 12px;
        min-width: 55px;

    }

    .room_item>.item_text_wrap .line_wrap {
        font-size: 16px;
        margin-top: 15px;
    }

    .room_info {
        font-size: 14px;
    }

    .booking {
        font-size: 14px;
    }
}

@media (max-width: 1024px) {

    #roomDetali.grid .room_list {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: 1rem;
    }

    .room_item {
        height: unset;
        display: flex;
        overflow: hidden;
        padding: 0;
        flex-direction: column-reverse;
    }

    .room_item>.item_text_wrap {
        padding: 20px;
    }


    .room_item .item_img_wrap {
        /* border-radius: 0px; */
    }
}

@media (max-width: 768px) {
    #roomDetali .room_list {
        gap: 2rem;

    }

    .room_item .item_img_wrap {
        height: 550px;
    }

    .room_item {
        gap: 0px;
    }
}