:root {
    --main-bg-color: #333;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: inherit;
    font-family: 'Noto Sans KR';
    font-weight: 500;
    text-transform: inherit;
}

.mobile {
    display: none !important;
}


a {
    text-decoration: none;
}

img,
video {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

input.c-input {
    outline: none;
    padding: 12px 14px;
    border: 1px solid #DADEE2;
    border-radius: 5px;
}

.c-btn-main {
    outline: none;
    padding: 12px 10px;
    border-radius: 4px;
    background-color: var(--main-bg-color);
    color: #fff;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.c-btn-black {
    outline: none;
    padding: 12px 10px;
    border-radius: 4px;
    background-color: #222;
    color: #fff;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.c-select {
    width: 100%;
    outline: none;
    padding: 12px 14px;
    border: 1px solid #DADEE2;
    border-radius: 5px;
}

.flex {
    display: flex;
}

.grid {
    display: grid;
}

.none {
    display: none !important;
}

.container {
    /* width: 1200px; */
    margin: 0 auto;
}

.title_wrap .main_title {
    font-size: 55px;
    font-weight: bold;
}

.title_wrap .sub_title {
    font-size: 22px;
    font-weight: bold;
    color: #BC9F7C;
}

/* swiper css  */
.swiper-pagination-bullet-active {
    background-color: #9c9c9c;
}

.scrollStyle .img_wrap_scroll::-webkit-scrollbar {
    height: 8px;
    /* border-radius: 4px; */
}

.scrollStyle .img_wrap_scroll::-webkit-scrollbar-thumb {
    height: 30%;
    /* 스크롤바의 길이 */
    background: #666;
    /* 스크롤바의 색상 */

    /* border-radius: 10px; */
}

.scrollStyle .img_wrap_scroll::-webkit-scrollbar-track {
    background: #d9d9d9;
    /*스크롤바 뒷 배경 색상*/
}



button {
    outline: none;
    border: none;
}

button.com_btn {
    background-color: #bc9f7c;
    color: #fff;
}

html {
    scroll-behavior: smooth;
}

