.container {
    width: 75%;
    max-width: 1600px;
    margin: 0 auto;
}

.title_wrap .sub_title {
    font-size: 22px;
    font-weight: bold;
    color: #BC9F7C;
}


@media (max-width: 768px) {
    .container {
        width: 90%;
    }

    .mobile {
        display: flex !important;
    }

    .pc {
        display: none !important;
    }
}